.headerWrap { position:fixed; top:0; left:0; width:100%; color:#fff;
    transition: all 0.5s;z-index: 9;
    .row {
        position:relative;
        display:flex; height:80px; justify-content:space-between;
        align-items: center; 
        // .logo, .nav, .mobileNav { align-self:center }
        .mobileNav { position:relative;
            button { padding:10px 20px; border-radius:50px;
                border:none; font-size:20px; display:flex;
                cursor:pointer; 
                .ham { margin-right:10px }       
                .ham, .person { align-self:center  }   
            }
            .mobileNavList { display:none;
                position:absolute; top:100%; right:0; 
                margin-top:10px;
                background:#fff; padding:10px 0px; 
                color:#000; width:150px; 
                border-radius:10px; 
                &.opener { display:block }
                ul {
                    li { line-height:30px; padding:0 20px }
                    li:nth-child(3) { 
                        border-top:1px solid #ddd; padding-top:10px;
                        margin-top:10px; 
                    }
                }
            }
        }
        .nav { position:absolute; left:50%; transform:translateX( -50%);
           .depth1 { display: flex;
               li { position:relative; padding:20px 0 ; cursor: pointer;  margin: 0 30px;}
           }
          .depth2 { display:none;
              position:absolute; top:100%; left:50px; transform: translate(-50%);
              width:150px; background:#fff; color:#000;
            &.on { display:block }
          }
        }
    }
}